<template>
  <div class="col-3">
    <div class="panel">
      <div class="panel-heading">
        <div class="panel-title">涉案金额分布</div>
      </div>
      <div class="panel-body" v-loading="loading" style="overflow: auto;">
        <div class="chart" id="chart2"></div>
      </div>
    </div>
  </div>
</template>

<script>
import watchDate from '@/mixins/watchDate'
export default {
  props: ['dateType'],
  data () {
    return {
      loading: false
    }
  },
  mixins: [
    watchDate
  ],
  mounted () {
    this.init()
  },
  methods: {
    init () {
      var self = this
      this.loading = true
      this.$http.get({
        api: 'A_S_CASE_AMOUNT',
        params: {
          dateType: this.dateType
        },
        loading: this.$data,
        callback: (res) => {
          self.loading = false
          self.draw(res)
        }
      })
    },
    draw (a) {
      let d = this.dataFormat(a)
      let option = {
        color: ['#3398DB'],
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: d.xAxis,
        yAxis: d.yAxis,
        series: d.series
      }
      let myChart = this.$echarts.init(document.getElementById('chart2'), 'macarons')
      myChart.setOption(option)
    },
    dataFormat (a) {
      let obj = {
        series: [],
        xAxis: [],
        yAxis: []
      }
      Array.prototype.slice.apply(a.series).map(o => {
        o.type = 'bar'
        obj.series.push(o)
      })
      obj.xAxis.push({
        type: 'category',
        data: a.category,
        axisLabel: {
          interval: 0,
          rotate: 40,
          formatter: function (value) {
            if (value.length > 5) {
              return value.substring(0, 5) + '...'
            }
            return value
          }
        }
      })
      obj.yAxis = [{
        type: 'value'
      }]
      return obj
    }
  }
}

</script>



